import React, {Component} from 'react';
import Pip from './Pip';

const arrow = require("../assets/arrowhead.svg");
const githubDark = require("../assets/githubDark.png");

export default class Showcase extends Component {
    constructor(props) {
        super(props);

        this.state = {
            currentIndex: 0,
            images: this.props.images,
            text: this.props.text,
            titles: this.props.titles,
            backgrounds: this.props.backgrounds,
            links: this.props.links,
            hideView: this.props.backgrounds ? false : null,
        }
    }

    nextSlide = () => {
        if(this.state.currentIndex === this.props.content.length - 1) {
            this.setState({currentIndex: 0})
            return;
        }
        this.setState({currentIndex: this.state.currentIndex + 1});
    }

    prevSlide = () => {
        if(this.state.currentIndex === 0) {
            this.setState({currentIndex: this.props.content.length - 1})
            return;
        }
        this.setState({currentIndex: this.state.currentIndex - 1});
    }

    setSlide = (num) => {
        if(this.state.currentIndex === num) {
            return;
        }
        this.setState({currentIndex: num});
    }

    handleKey = (e) => {
        let key = e.key.split('Arrow');
        if(key.length !== 2) {
            return;
        }
        switch(key[1]) {
            case 'Right':
                this.nextSlide();
                break;
            
            case 'Left':
                this.prevSlide();
                break;
            
            default:
                return;
        }
    }

    showBackground = () => {
        this.setState({ hideView: !this.state.hideView})
    }

    componentWillMount() {
        document.addEventListener("keydown", this.handleKey, false);
    }

    componentWillUnmount() {
        document.removeEventListener("keydown", this.handleKey, false);
    }

    render() {
        const slides = this.props.content;
        

        return (
            <div className={`${this.props.backgrounds ? 'showcaseplus' : 'showcase'}`}>
                {this.props.backgrounds && this.state.backgrounds.map((item, index) => 
                    <img className={`background ${this.state.currentIndex === index ? 'active' : ''} ${this.state.hideView ? 'unblur' : ''}`} src={item} alt={''} />
                )}
                <div className={`view ${this.state.hideView ? 'hidden' : ''}`}>
                    {slides.map((item, index) => 
                        <div className={`slide ${this.state.currentIndex === index ? 'active' :
                        this.state.currentIndex >= index ? 'next' :
                        this.state.currentIndex <= index ? 'prev' : ''}`} key={index}>
                            {item.link ? <a href={item.link} title={"Go to Repository"} target="_blank" rel="noopener noreferrer" className="image">
                                <img src={item.image} alt={`View ${index}`}/>
                            </a> : 
                            <div className="image" title={this.props.backgrounds ? "No Github link attached." : ""}>
                                <img src={item.image} alt={`View ${index}`}/>
                            </div>}
                            <div className="text">
                                <h2 className="title">{item.title}</h2>
                                {item.text}
                                {item.link && 
                                <a href={item.link} 
                                title={"Go to " + item.title} 
                                target="_blank" 
                                rel="noopener noreferrer" 
                                className={item.linkColor}
                                id="github"
                                style={{
                                    height: "4rem", 
                                    width: "4rem", 
                                    padding: "0.375rem", 
                                    borderRadius: "10px", 
                                    position: "absolute", 
                                    bottom: "3.15rem", 
                                    right: "3.15rem"}}>
                                    <img src={githubDark} 
                                    alt="Github Link" 
                                    title="Go to Repository"/>
                                </a>}
                            </div>
                        </div>
                    )}
                </div>
                <div className="slideNav">
                    <img src={arrow} id={'leftArrow'} 
                    className={this.state.currentIndex === 0 ? "poof" : "appear"} 
                    onClick={this.prevSlide} 
                    title="Go Back" 
                    alt={''}/>
                    <div className="pipBox">
                        {slides.map((item, index) => 
                            <Pip key={index} active={this.state.currentIndex === index} title={"Slide " + (index + 1)} onClick={() => this.setSlide(index)}/>
                        )}
                    </div>
                    <img src={arrow} id={'rightArrow'} 
                    className={this.state.currentIndex === this.props.content.length - 1 ? "poof" : "appear"} 
                    onClick={this.nextSlide} 
                    title="Next Slide" alt={''}/>
                </div>
                {this.props.backgrounds &&
                    <div className="hideView" 
                    title="Hover to See Background" 
                    onMouseEnter={this.showBackground} 
                    onMouseLeave={this.showBackground}>
                        Hover Here
                    </div>
                }
            </div>
        );
    }
}